}

.logo {
  display: flex;
}

.nav {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav_list {
  text-transform: uppercase;
  gap: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.moyo-header-logo {
  flex-shrink: 0;
